/* ============================= 1. RESET STYLES  ================================ */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; font: inherit; vertical-align: baseline; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block; }

body { line-height: 1; }

ol, ul { list-style: none; }

blockquote, q { quotes: none; }

blockquote:before, blockquote:after { content: ''; content: none; }

q:before, q:after { content: ''; content: none; }

table { border-collapse: collapse; border-spacing: 0; }

/* ============================= 2. GLOBAL STYLES  ================================ */
body { background: #fff; color: #65594d; font-family: "Tahoma","Open Sans",sans-serif; font-size: 15px; font-weight: 400; line-height: 1.6; }

a, a:hover { color: #65594d; text-decoration: none !important; -webkit-transition: 0.4s; -moz-transition: 0.4s; -ms-transition: 0.4s; -o-transition: 0.4s; transition: 0.4s; }

button { -webkit-transition: 0.4s; -moz-transition: 0.4s; -ms-transition: 0.4s; -o-transition: 0.4s; transition: 0.4s; }

b, strong { font-weight: bold !important; }

div, button, input, textarea, select { outline: none !important; }

ul, li, ol { list-style: none; margin: 0; padding: 0; }

.no-padding { padding: 0; }

h1 h2, h3, h4, h5, h6 { font-family: 'CormorantInfant', 'Open Sans', sans-serif; color: #65594d; }

h2, .h2 { font-size: 40px; margin: 0 0 5px; }

h3, .h3 { font-size: 24px; margin: 0 0 5px; }

.video-wrap { position: relative; padding-bottom: 56.25%; height: 0; overflow: hidden; }

.video-wrap iframe, .video-wrap object, .video-wrap embed { position: absolute; top: 0; left: 0; width: 100%; height: 100%; border: none; }

.container { width: 100% !important; max-width: 1200px !important; margin: 0 auto; }

.wrapper { width: 100%; position: relative; overflow: hidden; }

.btn { border: none; }

.btn-primary { background-color: #65594d; color: #c3b5a7; padding: 11px 25px; border-radius: 5px; font-size: 14px; }

.btn-primary:hover { background-color: #c3b5a7; color: #fff; }

.btn-default { background-color: #e6ddd0; color: #65594d; padding: 11px 25px; border-radius: 5px; font-size: 14px; }

.btn-default:hover { background-color: #65594d; color: #c3b5a7; }

/* ============================= 3. 404 STYLES  ================================ */
/* .error-content p { margin: 0 0 40px; line-height: 30px; } */
.error-content .error-head { margin: 0 0 50px; }

.error-content .error-head span { width: 270px; height: 270px; line-height: 270px; background-color: #e6ddd0; font-size: 130px; color: #c5b7a3; display: inline-block; border-radius: 50%; font-weight: 300; margin: 0 -28px; }

.error-content .error-head span:nth-child(2) { background-color: #c4b39a; color: #a39278; }

/* ============================= 4. CONTACT STYLES ================================ */
.contact .cinfo p { margin: 0 0 27px; }

.contact .cinfo p i { width: 15px; font-size: 10px; display: inline-block; }

.contact .cinfo p i.fa { font-size: 13px; }

.contact .cinfo p.abbr { line-height: 30px; }

.contact .contact-form { border: 2px solid #eaeaea; padding: 25px 50px 40px; }

.contact .contact-form i { font-size: 50px; margin: 0 0 10px; color: #e6ddd0; display: block; }

.contact .contact-form .h2 { margin: 0 0 30px; }

.contact .contact-form input { width: 100%; border: none; height: 30px; border-bottom: 2px solid #eaeaea; color: #65594d; font-size: 15px; margin: 0 0 28px; background: transparent; }

.contact .contact-form textarea { width: 100%; border: none; height: 95px; border-bottom: 2px solid #eaeaea; color: #65594d; font-size: 15px; margin: 0 0 34px; }

.contact .contact-form button { padding: 11px 40px; }

.contact .contact-form2 input { width: 100%; border: none; height: 47px; color: #939393; font-size: 14px; padding: 0 24px; margin: 0 0 20px; background: #f4f4f4; }

.contact .contact-form2 textarea { width: 100%; border: none; height: 178px; border-bottom: 2px solid #eaeaea; color: #939393; font-size: 14px; padding: 19px 24px; margin: 0 0 20px; background: #f4f4f4; }

.contact .contact-form2 button { padding: 11px 40px; }

.contact-form input::-webkit-input-placeholder { color: #65594d; }

.contact-form input:-moz-placeholder { color: #65594d; }

.contact-form input::-moz-placeholder { color: #65594d; }

.contact-form input:-ms-input-placeholder { color: #65594d; }

.contact-form textarea::-webkit-input-placeholder { color: #65594d; }

.contact-form textarea:-moz-placeholder { color: #65594d; }

.contact-form textarea::-moz-placeholder { color: #65594d; }

.contact-form textarea:-ms-input-placeholder { color: #65594d; }

/* ============================= 5. GOOGLE MAP ================================ */
#map { height: 250px; width: 100%; background: #f2f2f2; }

/* ============================= 6. FAQ ================================ */
.accordion .panel { margin-bottom: 0; padding-bottom: 5px; border-radius: 0; border: none; background: transparent; box-shadow: none; }

.accordion .accordion-toggle { display: table; width: 100%; font-family: Oxygen; font-weight: bold; color: #65594d; padding: 13px 40px 13px 0; border: none; border-bottom: 1px solid #eeeeee; font-size: 16px; position: relative; }

.accordion .accordion-toggle .indicator { position: absolute; right: 0; font-size: 10px; top: 50%; margin-top: -4px; color: #000; }

.accordion .panel-body { padding: 20px 0 0; }

.accordion .panel-body p { font-size: 15px; line-height: 24px; margin: 0 0 25px; }

.accordion .panel-body p:last-child { margin: 0 0 5px; }

/* ============================= 7. SUBPAGES ================================ */
.resources .ri-thumb { position: relative; cursor: pointer; -webkit-transition: 0.4s; -moz-transition: 0.4s; -ms-transition: 0.4s; -o-transition: 0.4s; transition: 0.4s; }

